import React, {FunctionComponent} from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { useStaticQuery, graphql } from 'gatsby'

const SEO: FunctionComponent<any> = ({ description, meta, keywords, title }: any) => {
    const data = useStaticQuery(graphql`
        query {
            allMdx (filter: {internal: {type: {eq: "Mdx"}}, frontmatter: {published: {eq: true}}}) {
                distinct(field: frontmatter___tags)
            }
            site {
                siteMetadata {
                    title
                    description
                    author
                }
            }
        }
    `)
    const {
        title: siteTitle,
        description: siteDescription,
        author,
    } = data.site.siteMetadata

    keywords = keywords.concat(data.allMdx.distinct);
    const metaTitle = title || siteTitle
    const metaDescription = description || siteDescription

    return (
        <Helmet
            htmlAttributes={{
                lang: "zh-tw"
            }}
            title={metaTitle}
            titleTemplate={title ? `${title} :: ${siteTitle}` : siteTitle}
            meta={[
                {
                    name: `description`,
                    content: metaDescription,
                },
                {
                    property: `og:title`,
                    content: metaTitle,
                },
                {
                    property: `og:description`,
                    content: metaDescription,
                },
                {
                    property: `og:type`,
                    content: `website`,
                },
                {
                    name: `twitter:card`,
                    content: `summary`,
                },
                {
                    name: `twitter:title`,
                    content: metaTitle,
                },
                {
                    name: `twitter:description`,
                    content: metaDescription,
                },
                {
                    name: `twitter:creator`,
                    content: author,
                },
            ]
                .concat(
                    keywords.length > 0
                        ? {
                            name: `keywords`,
                            content: keywords.join(`, `),
                        }
                        : [],
                )
                .concat(meta)}
        />
    )
}

SEO.defaultProps = {
    lang: `zh`,
    meta: [],
    keywords: [
        'bruno',
        'yi-shiuan',
        'blog'
    ],
}

SEO.propTypes = {
    description: PropTypes.string,
    lang: PropTypes.string,
    meta: PropTypes.array,
    keywords: PropTypes.arrayOf(PropTypes.string),
    title: PropTypes.string,
}

export default SEO